.landing {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}

.about {
  background-image: url(./../../assets/backgroundMachine.png);
  background-size: 100% 100%;
  border: 1px solid black;
  border-radius: 15px;
  padding: 45px;
  min-height: 600px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50% 50%;
      grid-template-columns: 50% 50%;
}

.about__info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: bisque;
  padding: 40px;
  border-radius: 20px;
  border: 2px solid black;
}

.about__title {
  font-size: 22px;
  font-weight: 800;
}

.about__text {
  font-size: 18px;
  font-weight: 400;
  width: 80%;
  margin-bottom: 20px;
}

.about__button {
  padding: 10px 15px;
  border-radius: 30px;
  background-color: black;
  color: white;
  width: 80%;
  text-align: center;
  font-size: large;
  font-weight: bolder;
  border: 2px solid black;
}

.about__button:hover {
  cursor: pointer;
  width: 80%;
  border: 2px dashed black;
  -webkit-transition: 0.1s;
  transition: 0.1s;
  background-color: white;
  color: black;
}

.about__picture {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.about__picture img {
  max-width: 500px;
  border-radius: 20px;
  border: 2px solid black;
}
