.table {
  font-size: 18px;
  border: 2px solid black;
  overflow-y: auto;
}

.table table th {
  background-color: blanchedalmond;
  border: 1px solid black;
  min-width: 50px;
}

.tableHeader {
  border: 3px solid black;
  padding: 10px 80px;
}

.tableHeader__addState {
  background-color: transparent !important;
  border: none !important;
}

.tableHeader__addState button {
  background-color: transparent;
  border: none;
  font-weight: 400;
}

.tableHeader__addState button:hover {
  cursor: pointer;
  font-weight: 700;
}

.tableHeader__el {
  padding: 10px 80px;
}

.tableHeader__el:not(:first-child):hover {
  background-color: #e17474;
  cursor: pointer;
}

.char {
  padding: 50px 10px;
  border: 1px solid black;
  margin: 0px;
  text-align: center;
}

.choose {
  border: 1px solid black;
}

.choose select {
  font-size: 20px !important;
  margin: 3px 3px;
  padding: 10px;
}

.notChoose {
  padding: 10px;
}

.addState {
  padding: 5px;
  font-size: 30px;
  border: 1px dashed #5f5f5f;
  text-align: center;
}

.addState:hover {
  background-color: silver;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.deleteRule {
  font-size: 20px !important;
  padding: 5px;
  background-color: #e57b7b;
  color: white;
}
